@mixin onlyForMobile {

    // @media screen and (max-width: 767px) {
    @media screen and (max-width: 991px) {
        @content
    }
}

@mixin mobileAndUp {

    @media screen and (min-width: 991px) {
    // @media screen and (min-width: 767px) {
        @content
    }
}

@mixin lwTransition($time:0.3s, $property:all) {
    -webkit-transition: $property $time ease-in-out;
    -moz-transition: $property $time ease-in-out;
    -ms-transition: $property $time ease-in-out;
    -o-transition: $property $time ease-in-out;
    transition: $property $time ease-in-out;
}

body {
    // font-size: .875rem;
    -webkit-font-smoothing: antialiased;
    font-family: 'Roboto', sans-serif;
    padding-bottom: 36px;
    color: #1a1a1a;
    font-weight: 400;
}

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
    font-weight: 300;

    &.card-title {
        font-weight: 400;
    }
}

i.fa {
    font-style: normal;
}


.navbar {
    margin-bottom: 20px;
}

.lw-main-container {
    margin-bottom: 100px;
}

.lw-main-page-container {
    margin-top: 100px;
}

.bd-placeholder-img {
    font-size: 1.125rem;
    text-anchor: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

@media (min-width: 768px) {
    .bd-placeholder-img-lg {
        font-size: 3.5rem;
    }
}

.lw-page-heading {
    font-size: 25px;
    margin-bottom: 20px;
    /*text-transform: uppercase;*/

    border: 0;
    border-bottom: 1px solid rgba(0, 0, 0, .1);
}

.lw-footer {
    position: fixed;
    clear: both;
    display: block;
    z-index: 5;
    bottom: 0;
    width: 100%;
    border-top: 1px solid #ddd;
    background-color: #f8f9fa;
    padding: 10px 10px;
    text-align: center;
}


.lw-main-navbar {
    direction: ltr;
    border-bottom: 1px solid #e2e7f7;
    padding: 0 36px;
    .navbar-toggler {
        @media screen and (max-width: 1191px) {
            margin: 20px 10px 20px 0;
        }
    }
    @media screen and (min-width: 1192px) {
        height: 80px;
    }
}

.lw-nav-bg {
    background-color: #ffffff;
}

.lw-article-search-container {
    .lw-search_articles {
        width: 100%;
    }
}

a>img {
    outline: none;
}

.lw-document-sidebar {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    z-index: 100;
    background-color: #ffffff;
    padding-right: 36px;
    // border-right: 1px solid #e2e7f7;
    overflow: auto;
    padding-bottom: 100px;
    @include lwTransition();
    padding-top: 100px;

    @media print {
        position: relative;
    }
}

@include onlyForMobile {
    .lw-hidden-sm {
        display: none;
    }

    .lw-sidebar-opened {
        .lw-document-sidebar {
            box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
            left: 0;
        }

        body {
            overflow: hidden;
        }

        .lw-document-body {
            overflow: hidden;
        }
    }

    .lw-document-body {
        overflow: hidden;
    }

    .lw-document-sidebar {
        width: 300px;
        left: -300px;
    }
}

#lwBackToTop {
    position: fixed;
    bottom: 10px;
    right: 10px;
    cursor: pointer;
    display: none;
}

.lw-sidebar-toggler {
    margin-right: 16px;
}

