.lw-document {
    padding: 16px 36px;
    line-height: 2em;
    margin-top: 20px;
    padding-top: 60px;
    text-align: initial;

    pre {
        background: #f7f7f7;
        border-radius: 4px;
        padding: 12px;
        border: 1px solid #e8e8e8;
        text-shadow: white 2px 2px 2px;
    }

    .lw-document-heading,
    .lw-document-sub-heading {
        color: #337ac5;
        // text-transform: capitalize;
        margin-left: 0 !important;
    }

    .lw-document-heading {
        border-bottom: 1px solid #ddd;
        padding-bottom: 8px;

        &:first-child {
            display: none;
        }
    }

    .lw-document-sub-heading {
        font-size: 2.5em;
        margin-top: 30px;
        margin-bottom: 20px;
        padding-bottom: 12px;
        border-bottom: 1px dotted #ddd;
        // font-size: 20px;
        // padding : 10px;
        // padding-left : 0px;
    }
}

.lw-document-body {
    @media screen and (min-width: 767px) {
        padding: 0 80px 0 50px;
    }

    img {
        max-width: 100%;
        height: auto;
    }
}

.lw-document-link {
    display: none;
}

.lw-document-link:hover {
    display: block;
}

.lw-document-hash-link {
    display: none;
}

.lw-document-sub-heading:hover .lw-document-hash-link {

    display: inline-block;

}


.lw-project-logo {
    max-height: 50px;
    // display: none;
}

.lw-project-item {
    a {
        text-decoration: none;
    }
}

.lw-project-logo-print {
    max-height: 250px;
    margin-bottom: 50px;
}

.lw-project-version-language-menu {
    right: 0;
    position: absolute;
}

.lw-toc-index,
.lw-side-links {
    direction: ltr;
    a {

        &:hover,
        &.lw-active-article,
        &:active {
            background-color: rgb(236, 236, 236);
            border-radius: 6px;
            color: #6f6f6f !important;
        }
        &.lw-active-article {
            pointer-events: none;
            user-select: none;
            cursor: arrow;
        }
    }
}

.lw-side-links {
    ul {
        list-style: none;
        padding: 0;
        margin: 0;

        a {
            @include lwTransition();
        }

        ul {
            // margin-left: 8px;

            a:before {
                content: ' \21B3 ';
                color: #323232;
                margin-right: 10px;
                // @include lwTransition();
            }

            a:hover {
                margin-left: 10px;
            }
        }

        a {
            text-decoration: none;
            display: inline-block;
            // text-transform: capitalize;
            width: 100%;
            padding: 2px 8px;
        }
    }
}

.lw-toc-index a {
    padding: 6px 10px;
    margin-bottom: 10px;
}

.lw-page-break {
    display: none;
}

.lw-cover-page {
    margin-top: 0;
    // display: none;
}

a {
    text-decoration: none !important;
}

//  Print CSS
@media print {
    html {
        background: transparent;
    }

    html,
    body.pt-4,
    .lw-document,
    .lw-document-sidebar {
        padding-top: 0 !important;
    }

    a {
        text-decoration: none !important;
    }

    .lw-article-search-container,
    .btn {
        display: none !important;
        visibility: hidden;
    }

    .row {
        display: block;
    }

    .lw-document-sidebar {
        display: none;
        // margin-bottom: 120px;
        // padding: 0;
        // min-height: 100vh;
    }

    .lw-page-break,
    .lw-document-child-content {
        display: block;
        page-break-before: always;
    }

    .lw-cover-page {
        margin-top: 200px;
        display: block;
    }

    .lw-table-of-content {
        display: block;
    }

}

.lw-version-slug {
    margin-top: 9px;
    margin-right: 10px;
    color: #7c7c7e;
}


.lw-project-list-card {
   .card-body {
       height: 250px;
   }

    &.card {
        margin: 0;
    }

    @include lwTransition();

    &:hover {
        background-color: #f7f7f7;
    }

    img {
        max-height: 200px;
        margin-top: 30px;
        max-width: 100%;
        object-fit: contain;
    }
}

.lw-project-title {
    color: #848484;
    margin-top: 16px;
    font-family: inherit;
    font-size: 2rem;
    display: none;
}

.lw-article-title {
    font-size: 2.8em;
    font-family: inherit;
    padding: 20px 0;
    font-weight: 300;
    color: #ff2d20;
    text-transform: capitalize;
}

.table-of-contents {
    h3 {
        font-weight: 300;
    }
}

.lw-table-of-content {
    margin-bottom: 100px;
    // display: none;
}

.lw-index-loader-spinner {
    border: 4px solid #f3f3f3;
    /* Light grey */
    border-top: 4px solid #3498db;
    /* Blue */
    border-radius: 50%;
    width: 16px;
    height: 16px;
    animation: lw-spin-anim 2s linear infinite;
    display: inline-block;
}

@keyframes lw-spin-anim {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.lw-menu-parent-item {
